import type { CommonPackage } from '@/core/common';
import type { v3Env, LoggerSender } from '@/config/types';

import { BatchLog as BaseLogger} from '@/core/logger';
import { sendData, get } from '@/io/node';
import { dataTrackHost, nodeBuildUrl } from '@/config/apis';
import { getReportUrl } from '@/util/test';
/**
 * env 为 logger 时上报的 url
 */
const getLoggerReportUrl = (did: string = 'krnlogger') => {
    get(`${dataTrackHost}/rest/${did}`);
    return getReportUrl(dataTrackHost, `${dataTrackHost}/rest`, did);
};
export default class Logger extends BaseLogger {
    override radarUrl: string = '';
    override baseSendData: LoggerSender = sendData;
    constructor(config: any, commonPackage: CommonPackage) {
        super(config, commonPackage);
    }
    /**
     * 根据配置生成上报的 url 地址
     */
    override updateUrls() {
        const { env, logger } = this.config;
        if (logger || env === 'logger') {
            const loggerEnv = getLoggerReportUrl(this.commonPackage.identity_package.device_id);
            this.url = nodeBuildUrl(loggerEnv);
        } else if (env && /^(https?:)?\/\//.test(env)) {
            this.url = env;
        } else {
            this.url = nodeBuildUrl(env as v3Env);
            this.radarUrl = nodeBuildUrl(env as v3Env, true);
        }
        this.formatUrl();
    }
}
