import type { HttpWeblogInitParams, UrlObj } from '@/types/configuration';
import type {
    EventType as IEventType,
    EventOptions as IEventOptions,
} from '@/types/event';
import BaseWeblog from '@/core';
import { CommonPackage } from '@/core/common';
import UrlPackage from '@/core/url-package';
import Logger from './logger';
import { logFactory } from '@/core/log';
import { uuid } from '@/util/uuid';
import { updateConfig } from '@/config/global';

export default class NodeWeblog extends BaseWeblog {
    declare logConfig: HttpWeblogInitParams[0];
    constructor(
        logConfig: HttpWeblogInitParams[0],
        base?: HttpWeblogInitParams[1],
    ) {
        super(logConfig, base);
        updateConfig('sessionId', base && base.session_id || `nodesid_${uuid()}`);
        this.presetBaseOption(this.baseOption);
        this.commonPackage = new CommonPackage(this.baseOption);
        this.logger = new Logger(this.logConfig, this.commonPackage);
        this.currentUrlPackage = new UrlPackage({page: ''});
        this.addPlugins();
    }
    private presetBaseOption(base: {[key: string]: any}) {
        if (!base.device_id) {
            base.device_id = `nodedid_${uuid()}`;
        }
    }
    /**
     * 更新当前 url 信息
     * @param url
     * @param type
     */
    override updateCurrentUrlPackage(url: UrlObj = {}) {
        // 该接口不改变 refer 信息
       if (typeof url === 'object' && !url.force && this.currentUrlPackage) {
            const { page = this.currentUrlPackage.page, params = {} } = url;
           if (page === this.currentUrlPackage.page) {
               return this.currentUrlPackage.update(page, params);
           }
       }
       this.currentUrlPackage = new UrlPackage(url);
   }

   /**
    * 更新 refer url 信息
    * @param url
    * @param type
    */
    override updateReferUrlPackage(url: UrlObj | UrlPackage = this.currentUrlPackage) {
       if (url instanceof UrlPackage) {
           this.referUrlPackage = url;
       } else {
           this.referUrlPackage = new UrlPackage(url);
       }
    }

    override generateLog<T extends IEventType>(
        eventType: T,
        eventOptions: IEventOptions<T>,
        factory: (type: string, data: any) => any = logFactory
    ) {
        let { action, params, status, type, eventId, contentPackage, urlPage } = eventOptions as IEventOptions<'CLICK'>;
        let currentUrlPackage;
        if (contentPackage) {
            contentPackage = typeof contentPackage === 'string' ? contentPackage : JSON.stringify(contentPackage);
        }
        // 若是 pv 先更新currentUrlPackage
        if (eventType === 'PV') {
            const { page } = eventOptions as IEventOptions<'PV'>;
            this.currentUrlPackage.update(page, params);
        } else if (urlPage && urlPage.page) {  // 如果参数中带有 urlPage，用来解决页面飘移问题，只影响该埋点
            currentUrlPackage = {
                page: urlPage.page,
                identity: urlPage.identity || uuid(),
                params: JSON.stringify(urlPage.params),
                page_type: this.currentUrlPackage.page_type
            }
        }
        const commonInfo = {
            currentUrlPackage: currentUrlPackage || this.currentUrlPackage.toJSON(),
            referUrlPackage: this.referUrlPackage ? this.referUrlPackage.toJSON() : undefined,
            contentPackage
        };
        if (eventType === 'PV') {
            const { type } = eventOptions as IEventOptions<'PV'>;
            // PV 类型
            return factory(eventType, {
                ...eventOptions as IEventOptions<'PV'>,
                type,
                name: eventType,
                ...commonInfo
            });
        }
        if (eventType === 'SHOW') {
            // SHOW 类型
            return factory(eventType, {
                ...eventOptions,
                name: action,
                ...commonInfo
            });
        }

        if (eventType === 'VIDEO') {
            return factory(eventType, {
                params: eventOptions,
                name: 'VIDEO',
                ...commonInfo
            });
        }

        // Radar 日志特殊处理
        if (eventType === 'RADAR') {
            return factory(eventType, eventOptions);
        }

        if (eventType === 'CUSTOM') {
            // CUSTOM 类型
            const { key, value } = eventOptions as IEventOptions<'CUSTOM'>;
            return factory(eventType, {
                ...eventOptions,
                params: value,
                name: key,
                ...commonInfo
            });
        }

        // TASK 类型
        return factory(eventType, {
            ...eventOptions,
            params,
            status,
            taskType: type,
            eventId,
            name: action,
            ...commonInfo
        });
    }
}