import type { DataOptions } from './types';
import { logXhrResponseCheck, xhrErrorLog } from './util';
import { error } from '@/util/console';
import https from 'https';
let isNode = typeof window === 'undefined' && typeof process === 'object';

export const get = (url: string) => {
    if (!isNode) return;
    const errorHandler = (err: any) => error('[error 401]', `GET 请求出错 url: ${url}`, err);
    try {
        const req = https.get(url);
        req.on('error', errorHandler);
    } catch (err) {
        errorHandler(err);
    }
};

export const sendData = ({ url, data, timeout }: DataOptions, callback?: (error?: any) => void) => {
    if (!isNode) return;
    data = JSON.stringify(data);
    const options = {
        method: 'POST',
        timeout,
        headers: {
            'Content-Type': 'text/plain;charset=UTF-8',
            'Content-Length': Buffer.from(data).length
        }
    };
    const req = https.request(url, options, (res: any) => {
        if (res.statusCode === 200) {
            let resData = '';
            res.on('data', (chunk: any) => resData += chunk);
            res.on('end', () => {
                logXhrResponseCheck(200, resData, url, data);
                callback && callback();
            });
        } else {
            res.statusCode && xhrErrorLog(res.statusCode, url, data);
            callback && callback(res.statusCode);
        }
    });
    req.on('timeout', (e: any) => {
        e && xhrErrorLog(e, url, data);
        callback && callback(e)
    });
    req.on('error', (e: any) => {
        e && xhrErrorLog(e, url, data);
        callback && callback(e)
    });
    req.write(data);
    req.end();
};
